@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

:root {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --text-color-1: #fff;
  line-height: 1.4;
  font-size: 16px; }

.wrapper {
  width: 90%;
  margin: 0 auto; }

/* HEADER */
.header {
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  height: 50px; }

.header-icon {
  transform: scale(0.7);
  height: 90%;
  padding: 5px 0; }

.menu-nav {
  width: 60%;
  margin-right: 20px; }

.menu {
  list-style: none;
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-weight: 700; }

.hero {
  color: var(--text-color-1);
  background-color: #47a2f2;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center; }

.main-title {
  font-size: 2.8rem;
  font-weight: 700;
  margin-top: 25%;
  margin-bottom: 10px; }

.button {
  width: 100%;
  font-weight: 600;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  padding: 15px 0;
  background-color: #01a001;
  color: #fff;
  border-radius: 30px;
  border: none;
  margin-bottom: 25px; }

.hero-text {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.6; }

.terms {
  font-size: 10px;
  font-weight: 700;
  margin-bottom: 25px; }

/* BENEFIT */
.benefit-section {
  padding: var(--padding-left); }

.section-title {
  text-align: center; }

.benefit {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.benefit-text {
  width: 75%; }

.benefit-img {
  width: 25%;
  padding: var(--padding-left); }

.benefit-img img {
  width: 90%; }

/* FOOTER */
.footer {
  color: var(--text-color-1);
  background-color: black;
  padding: var(--padding-left); }

.footer a {
  color: var(--text-color-1);
  text-decoration: none; }

.footer-row-1 {
  display: flex;
  justify-content: end;
  align-items: baseline; }

.footer-row-1 img {
  padding-left: var(--padding-left);
  transform: scale(0.8) translateY(25%); }

/* -- Versi贸n tablet -- */
@media all and (min-width: 480px) {
  .menu-icon {
    display: none; } }

